import { makeStyles } from "@material-ui/core/styles";
import { ThemeJSON } from "../../../../components/IFormDesignerLayout/Theme/ThemeJSON";

const useStyles3 = makeStyles({
  nav: {
    backgroundColor: (style) => style.InActiveBackgroundColor,
    border: (style) => `1px solid ${style.InActiveFontColor}`,
    display: "flex",
    flexDirection: (style) => (style.menuAlignment.toLowerCase() === "left" ? "row" : "row-reverse"),

    "& .menu": {
      backgroundColor: (style) => `${style.InActiveBackgroundColor} !important`,
    }
    // '& h1':{
    //   display : style => style.menuAlignment.toLowerCase()==="left" ? "none" : "inline-block"
    // }
  },
  responsiveIcon: {
    "& path": {
      fill: (style) => (style.ActiveFontColor === "#fff" ? style.InActiveFontColor : style.ActiveFontColor || "#000")
    }
  },
  submenu: {
    top: (style) =>
      `${Math.max(
        37 + (parseInt(style.ActiveFontSize) - 10) * 1.5,
        37 + (parseInt(style.InActiveFontSize) - 10) * 1.5
      )}px`
  },
  activeMenu_span: {
    backgroundColor: (style) => style.ActiveBackgroundColor,
    padding: "10px 1.5em"
  },
  menu_span: {
    backgroundColor: (style) => style.InActiveBackgroundColor,
    padding: "10px 1.5em",

    "&:hover": {
      color: (style) => style.ActiveFontColor,
      backgroundColor: (style) =>
        style.ActiveBackgroundColor.length === 7
          ? `${style.ActiveBackgroundColor}bd`
          : style.ActiveBackgroundColor.length === 9
          ? style.ActiveBackgroundColor.slice(0, -2) + "bd"
          : style.ActiveBackgroundColor
    }
  },
  activeSubMenu_span: {
    backgroundColor: (style) =>
      style.ActiveBackgroundColor.length === 7
        ? `${style.ActiveBackgroundColor}bd`
        : style.ActiveBackgroundColor.length === 9
        ? style.ActiveBackgroundColor.slice(0, -2) + "bd"
        : style.ActiveBackgroundColor,
    padding: "10px 1.5em",
    alignItems: "center"
  },
  subMenu_span: {
    backgroundColor: (style) => style.InActiveBackgroundColor,
    padding: "10px 1.5em",
    alignItems: "center",

    "&:hover": {
      color: (style) => style.ActiveFontColor,
      backgroundColor: (style) =>
        style.ActiveBackgroundColor.length === 7
          ? `${style.ActiveBackgroundColor}bd`
          : style.ActiveBackgroundColor.length === 9
          ? style.ActiveBackgroundColor.slice(0, -2) + "bd"
          : style.ActiveBackgroundColor
    }
  },
  activeMenu_li: {
    // padding:"10px 1.5em",
    margin: "3px 0",

    color: (style) => style.ActiveFontColor,
    fontSize: (style) => style.ActiveFontSize === "Select" ? ThemeJSON.Menu.ActiveFontSize : style.ActiveFontSize,
    fontFamily: (style) => style.ActiveFontFamily === "Select" ? ThemeJSON.Menu.ActiveFontFamily : style.ActiveFontFamily,
    fontWeight: (style) => style.ActiveFontWeight === "Select" ? ThemeJSON.Menu.ActiveFontWeight : style.ActiveFontWeight,
    fontStyle: (style) => style.ActiveFontStyle === "Select" ? ThemeJSON.Menu.ActiveFontStyle : style.ActiveFontStyle,
    // borderTop: (style) => `1px solid ${style.ActiveFontColor}`,
    // backgroundColor: style => style.ActiveBackgroundColor,

    "& span": {
      width: "100%",
      display: "flex",
      boxSizing: "border-box",
      alignItems: "center"
    }
  },
  menu_li: {
    color: (style) => style.InActiveFontColor,
    fontSize: (style) => style.InActiveFontSize === "Select" ? ThemeJSON.Menu.InActiveFontSize : style.InActiveFontSize,
    fontFamily: (style) => style.InActiveFontFamily === "Select" ? ThemeJSON.Menu.InActiveFontFamily : style.InActiveFontFamily,
    fontWeight: (style) => style.InActiveFontWeight === "Select" ? ThemeJSON.Menu.InActiveFontWeight : style.InActiveFontWeight,
    fontStyle: (style) => style.InActiveFontStyle === "Select" ? ThemeJSON.Menu.InActiveFontStyle : style.InActiveFontStyle,
    // backgroundColor: style => style.InActiveBackgroundColor,
    // padding:"10px 1.5em",
    // margin: "3px 0",
    "&:hover": {
      // color: (style) => style.ActiveFontColor,
      fontSize: (style) => style.ActiveFontSize === "Select" ? ThemeJSON.Menu.ActiveFontSize : style.ActiveFontSize,
      fontFamily: (style) => style.ActiveFontFamily === "Select" ? ThemeJSON.Menu.ActiveFontFamily : style.ActiveFontFamily,
      fontWeight: (style) => style.ActiveFontWeight === "Select" ? ThemeJSON.Menu.ActiveFontWeight : style.ActiveFontWeight,
      fontStyle: (style) => style.ActiveFontStyle === "Select" ? ThemeJSON.Menu.ActiveFontStyle : style.ActiveFontStyle,
      // backgroundColor: style => style.ActiveBackgroundColor.length === 7 ? `${style.ActiveBackgroundColor}bd`: style.ActiveBackgroundColor.length === 9 ?  style.ActiveBackgroundColor.slice(0, -2) + 'bd' :style.ActiveBackgroundColor ,
    },
    "& span": {
      width: "100%",
      display: "flex",
      boxSizing: "border-box",
      alignItems: "center"
    }
  },
  submenu_li: {
    fontSize: (style) => style.InActiveFontSize === "Select" ? ThemeJSON.Menu.InActiveFontSize : style.InActiveFontSize,
    "& > span": {
      display: "flex",
      alignItems: "center",
      justifyContent: "space-between",
      "& > svg": {
        position: "absolute",
        right: "0px"
      }
    }
  }
});

export default useStyles3;
