import React from "react";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  custombutton: {
    fontFamily: (style) => `${style.ButtonFontFamily} !important`,
    fontSize: (style) => `${style.ButtonFontSize} !important`,
    fontWeight: (style) => style.ButtonFontWeight,
    color: (style) => style.ButtonFontColor,
    border: (style) => `${style.ButtonBorderWidth} solid ${style.ButtonBorderColor || "#000"}`,
    background: (style) => style.ButtonBackgroundColor,
    borderRadius: (style) => `${style.ButtonBorderRadius} !important`,
    fontStyle: (style) => style.ButtonFontStyle,
    padding: "5px 8px",
    margin: "0 5px",
    height: "unset !important"
  }
});

//   ButtonBackgroundColor: "#8b572a"
// ButtonBorderRadius: "4px"
// ButtonBorderWidth: "5px"
// ButtonFontColor: "#d0021b"
// ButtonFontFamily: "Sans-Serif"
// ButtonFontSize: "13px"
// ButtonFontWeight: 300

function CustomButton({ label, style }) {
  const classes = useStyles(style);

  return <button className={classes.custombutton}>{label}</button>;
}

export default CustomButton;
