import React, { useState, useEffect, useRef, useLayoutEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import { useMediaQuery } from "@material-ui/core";

import "./index.css";
import CustomButton from "./components/CustomButton";
import useStyles1 from "./styles/style1";
import useStyles2 from "./styles/style2";
import useStyles3 from "./styles/style3";
import ELEMENTS from "../ELEMENTS";
import ElemDeleteControl from "../ElemDeleteControlWrapper";
import axios from "../../../axios";
import DownArrow from "../../../Icons/DownArrow";
import { ReactComponent as Hamburger } from "../../../Icons/Hamburger.svg";
import { ReactComponent as BlackCrossMenu } from "../../../Icons/BlackCrossMenu.svg";
import { checkValid, isObjectEmpty } from "../../../Resources/globalFunctions";
import { changeControlProperty } from "../../../components/IFormDesignerLayout/IFormDesignerLogic";
import { GetState, GetStateFromData } from "../../../components/CustomHooks/CustomHooks";
import QuickTools from "../../../ReusableComponents/QuickTools/QuickTools";
import CustomizedAlert from "../../../ReusableComponents/Alert/CustomizedAlert";

const useStyles = makeStyles({
  menuMain: {
    backgroundColor: (style) => style.menuBackground,
    border: (style) => `${style.BorderWidth || "1px"} solid ${style.BorderColor || "#C6C9CF"}`,
    borderRadius: (style) => style.BorderRadius || "1px"
  },
  li_Active: {
    backgroundColor: "#c8dcf9",
    color: "#0072c6 !important"
  }
});

function Menu(props) {
  const contexts = GetStateFromData(props.containerId);
  const { tempCanWidth, controlSelected }  = GetState("tempCanWidth", "controlSelected");

  const {
    updateJson,
    activeApplication,
    deleteControl,
    setQuickToolsClose,
    isMenuSubMenuAdded,
    setActiveMenuId,
    activeMenuId,
    imageBaseURL,
    set_wizard_type_step,
    set_wizard_type_subStepIndex,
    set_wizard_type_subChildMenuIndex,
    openQuickToolFor
  } = contexts.contexts;

  const translationStyles1 =
    window?.appformBuilder_configs?.selectedLanguage === "ar" ||
    window?.appformBuilder_configs?.selectedLanguage === "ar-SA";

  const { updatedComponentInFormState } = updateJson;
  const style = props.data.style;
  const data = props.data;
  const conditionalQuickToolOpening = props.id === openQuickToolFor ? true : false;

  const outerNav = useRef(null);
  const nav = useRef(null);

  const [open, setOpen] = useState(conditionalQuickToolOpening);
  const [menuHeight, setMenuHeight] = useState(0);
  const [activeId, setActiveId] = useState("");
  const [activeJson, setActiveJson] = useState({});
  const [dataArray, setDataArray] = useState();
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification"
  });
  const [minimisedMenu, setMinimisedMenu] = useState(false);
  const [openMenu, setOpenMenu] = useState(false);
  const [navItems, setNavItems] = useState([...props.data.children]);
  const [navStaticWidth, setNavStaticWidth] = useState(nav?.current?.offsetWidth);

  const classes1 = useStyles1({ ...style, minimisedMenu, translationStyles1 });
  const classes2 = useStyles2({ ...style, minimisedMenu, translationStyles1 });
  const classes3 = useStyles3({ ...style, minimisedMenu, translationStyles1 });
  const classes = useStyles(style);

  useEffect(() => {
    if (props.id === openQuickToolFor) {
      setOpen(true);
    } else {
      setOpen(false);
    }
  }, [openQuickToolFor]);

  function getStyles() {
    if (props.data.style.layout === "style1") {
      return classes1;
    } else if (props.data.style.layout === "style2") {
      return classes2;
    } else if (props.data.style.layout === "style3") {
      return classes3;
    }
  }

  const iconHeight = `calc(${style.ActiveFontSize || "12px"} - 5px + 24px)`;
  const crossIconHeight = `calc(${style.ActiveFontSize || "12px"} - 5px + 10px)`;

  const matches = useMediaQuery(`(max-width: ${nav?.current?.offsetWidth}px)`);
  const mobileWidthMatches = useMediaQuery(`(min-width: 500px)`) || tempCanWidth < 500;

  var inputStylePadding = {
    paddingLeft: props.data?.style?.["SpacingLeft"],
    paddingRight: props.data?.style?.["SpacingRight"],
    paddingTop: props.data?.style?.["SpacingTop"],
    paddingBottom: props.data?.style?.["SpacingBottom"]
  };

  useEffect(() => {
    if (activeJson?.MainMenuIndex || activeJson?.MainMenuIndex === 0) {
      set_wizard_type_step(activeJson?.MainMenuIndex?.toString());
    } else {
      set_wizard_type_step("-1");
    }
    if (activeJson?.SubMenuIndex || activeJson?.SubMenuIndex === 0) {
      set_wizard_type_subStepIndex(activeJson?.SubMenuIndex?.toString());
    } else {
      set_wizard_type_subStepIndex("-1");
    }
    if (activeJson?.subChildMenuIndex || activeJson?.subChildMenuIndex === 0) {
      set_wizard_type_subChildMenuIndex(activeJson?.subChildMenuIndex?.toString());
    } else {
      set_wizard_type_subChildMenuIndex("-1");
    }
  }, [activeJson?.MainMenuIndex, activeJson?.SubMenuIndex, activeJson?.subChildMenuIndex]);

  const doesItFit = () => {
    if (outerNav.current && navItems) {
      const navWidth = nav.current?.offsetWidth;
      const outerWidth = outerNav.current?.offsetWidth;

      if (matches || navWidth > outerWidth || navWidth > tempCanWidth || navStaticWidth > tempCanWidth) {
        setMinimisedMenu(true);
      } else {
        setMinimisedMenu(false);
      }
    }
  };

  function handleActiveId(e, menuJson, menuItem) {
    let check = false;
    if (menuJson && menuItem) {
      check = isChildrenPresent(menuJson, menuItem);
    }
    if (check) {
      return;
    }

    setOpenMenu(false);

    setQuickToolsClose(true, () => {
      setTimeout(() => {
        setQuickToolsClose(false);
      }, 500);
    });

    if (typeof e === "string") {
      setActiveId(e);
      setActiveMenuId(e);
      return;
    }

    if (e) {
      e.stopPropagation();
      let id = e.target.getAttribute("listitemid");
      if (id === null) {
        id = e.currentTarget.closest(`li`).getAttribute("listitemid");
      }
      setActiveId(id);
      setActiveMenuId(id);
    }
  }

  window.openMenuItem = handleActiveId;

  let foundjson = {};

  function activeIdJson(formStatejson, activeId) {
    if (formStatejson && activeId) {
      if (formStatejson.id === activeId) {
        foundjson = formStatejson;
      } else {
        if (formStatejson.children && formStatejson.children.length) {
          formStatejson.children.map((child) => {
            if (child && child.id) {
              if (child.id === activeId) {
                foundjson = child;
              } else if (isObjectEmpty(foundjson)) {
                activeIdJson(child, activeId);
              }
            }
          });
        }
      }
      return foundjson;
    }
  }

  const saveAsTemplate = async () => {
    const data_Buffer = await provideData();
    setDataArray(data_Buffer);
    return data_Buffer;
  };

  const provideData = async () => {
    if (activeJson) {
      const subformName = activeJson?.subformName;

      if (subformName && activeApplication?.applicationId) {
        const buffer = await axios
          .get(`widget/${activeApplication?.applicationId}/${subformName}`, { withCredentials: true })
          .then((response) => {
            var formBuffer = JSON.parse(response.data.fragmentBuffer);
            const filtered_buffer = formBuffer.children[1].children;
            return filtered_buffer;
          })
          .catch((err) => {
            return activeJson?.children;
          });

        return buffer;
      } else {
        return activeJson?.children;
      }
    }
  };

  var menuIndex;
  var submenuIndex;
  var subchildmenuIndex;

  function setActiveIdOnLoad(isMenuAddedOrDeleted) {
    menuIndex = giveChildrenIndex(props.data, "MainMenu");
    submenuIndex = giveChildrenIndex(props.data?.children?.[menuIndex], "submenu");
    subchildmenuIndex = giveChildrenIndex(props.data.children?.[menuIndex]?.children?.[submenuIndex], "subchildmenu");

    if (activeMenuId && isMenuAddedOrDeleted) {
      setActiveId(activeMenuId);
    } else if (!isChildrenPresent(props.data.children?.[menuIndex], "submenu")) {
      setActiveId(props.data.children?.[menuIndex]?.id);
      setActiveMenuId(props.data.children?.[menuIndex]?.id);
    } else if (!isChildrenPresent(props.data.children?.[menuIndex]?.children?.[submenuIndex], "subchildmenu")) {
      setActiveId(props.data.children?.[menuIndex]?.children?.[submenuIndex]?.id);
      setActiveMenuId(props.data.children?.[menuIndex]?.children?.[submenuIndex]?.id);
    } else {
      setActiveId(props.data.children?.[menuIndex]?.children?.[submenuIndex]?.children?.[subchildmenuIndex]?.id);
      setActiveMenuId(props.data.children?.[menuIndex]?.children?.[submenuIndex]?.children?.[subchildmenuIndex]?.id);
    }
  }

  function getExtraDetails() {
    if (activeJson.elem_type === "MainMenu") {
      const json = {
        stepIndex: activeJson?.MainMenuIndex,
        substepIndex: "-1",
        subChildMenuIndex: "-1",
        elem_type: activeJson?.elem_type,
        id: activeJson?.id
      };
      return json;
    } else if (activeJson.elem_type === "SubMenu") {
      const json = {
        stepIndex: activeJson?.MainMenuIndex,
        substepIndex: activeJson?.SubMenuIndex,
        subChildMenuIndex: "-1",
        elem_type: activeJson?.elem_type,
        id: activeJson?.id
      };
      return json;
    } else if (activeJson.elem_type === "SubChildMenu") {
      const json = {
        stepIndex: activeJson?.MainMenuIndex,
        substepIndex: activeJson?.SubMenuIndex,
        subChildMenuIndex: activeJson?.subChildMenuIndex,
        elem_type: activeJson?.elem_type,
        id: activeJson?.id
      };
      return json;
    }
  }

  function getActiveJson() {
    return activeJson;
  }

  function isChildrenPresent(json, find) {
    const findKey = find?.toLowerCase();
    return (
      json?.children?.findIndex(function (child) {
        return child.elem_type.toLowerCase() === findKey;
      }) !== -1
    );
  }

  function giveChildrenIndex(json, find) {
    const findKey = find?.toLowerCase();
    return json?.children?.findIndex(function (child) {
      return child.elem_type.toLowerCase() === findKey;
    });
  }

  function isActiveElement(id) {
    if (activeId) {
      const current = document.querySelector(`#${activeId}`);
      const idMainMenu = current?.closest(`[elemType=MainMenu]`)?.id;
      const idSubMenu = current?.closest(`[elemType=SubMenu]`)?.id;

      return id === activeId || id === idMainMenu || id === idSubMenu;
    }
  }

  function handleDropOnMenu(e) {
    if (activeId) {
      props.handleReorderDrop(e, props.data.elem_type, props.data);
    } else {
      setAlertData({
        open: true,
        showConfirm: "showNotification",
        alertType: "error",
        alertContentMsg: "Please select Menu first"
      });
    }
  }

  const menuIconHandler = () => {
    setOpenMenu((prev) => !prev);
  };

  useLayoutEffect(() => {
    document.querySelector("main")?.click();
  }, []);

  useEffect(()=>{
    if(!props?.data?.containerControl && document.querySelector(".menuMain_Id")){
        props.data.containerControl= "Menu";
        updatedComponentInFormState();
    }
},[])

  useEffect(() => {
    setActiveIdOnLoad(!!activeMenuId);
  }, [isMenuSubMenuAdded, props.data.children.length]);

  useEffect(() => {
    doesItFit();
  }, [props.data.children, navItems, tempCanWidth, isMenuSubMenuAdded]);

  useEffect(() => {
    setNavStaticWidth((prev) => nav?.current?.offsetWidth || prev);
  }, [props.data.children, navItems]);

  useEffect(() => {
    window.setTimeout(() => {
      document.querySelector("#RootControl")?.click();
    }, 1000);
  }, []);

  useEffect(() => {
    if (props.previewmode !== true) {
      saveAsTemplate();
    }
  }, [props.data.children, activeJson, activeJson?.subformName]);

  useEffect(() => {
    if (props.previewmode === true) {
      saveAsTemplate();
    }
  }, [props.data.children, activeJson, activeJson?.subformName]);

  useEffect(() => {
    setNavItems(props.data.children);
  }, [props.data.children, data.children]);

  useEffect(() => {
    const json = activeIdJson(props.data, activeId);
    setActiveJson({ ...json });
  }, [props.data.children, activeId]);

  useEffect(() => {
    const height = document.querySelector(".menuMain_Id")?.getBoundingClientRect()?.top;
    setMenuHeight(height + 10);
  }, []);

  /* ***************** all useEffects end ***************** */

  /* ***************** Enable, ReadOnly and ReadOnlyStyle useEffects start ***************** */

  useEffect(() => {
    if (
      props.data.children.length > 0 &&
      (props.data.style.Enable === true ||
        props.data.style.Enable === false ||
        props.data.style.Enable === "true" ||
        props.data.style.Enable === "false")
    )
      changeControlProperty(props.data.style.Enable, "Enable", props.data);
      if (
        props.data.children.length > 0 &&
        (props.data.style.ReadOnly === true ||
          props.data.style.ReadOnly === "true" ||
          props.data.style.ReadOnly === false ||
          props.data.style.ReadOnly === "false")
      )
        changeControlProperty(props.data.style.ReadOnly, "ReadOnly", props.data);
      if (
        props.data.children.length > 0 &&
        (props.data.style.ReadOnlyStyle === "true" ||
          props.data.style.ReadOnlyStyle === true ||
          props.data.style.ReadOnlyStyle === false ||
          props.data.style.ReadOnlyStyle === "false")
      )
        changeControlProperty(props.data.style.ReadOnlyStyle, "ReadOnlyStyle", props.data);
      const isComposed = window?.event?.composed;

      if (isComposed) {
        updatedComponentInFormState();
      } else {
        updatedComponentInFormState(undefined, undefined, undefined, false);
      }
  }, [props.data.style.Enable,props.data.style.ReadOnly,props.data.style.ReadOnlyStyle]);

  /* ***************** Enable, ReadOnly and ReadOnlyStyle useEffects end ***************** */

  const isMobileMinimisedMenu = minimisedMenu && mobileWidthMatches;

  const MenuJSX = (
    <div
      tabIndex={props.previewmode === true ? "none" : "1"}
      id={props.id}
      control_id={props.id}
      className="menu_main childmf-formb"
      style={inputStylePadding}
      elem_type="Menu"
      onClick={(e) => props.handleSelected(e, props.id, "n")}
      onKeyDown={(e) => props.handleSelected(e)}
      readonly={props.data.style.ReadOnlyStyle}
      readOnlyValue={props.data.style.ReadOnly}
    >
      <header
        control_id={props.id}
        style={
          minimisedMenu
            ? {
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                ...(style?.menuAlignment === "right" ? { flexDirection: "row-reverse" } : {})
              }
            : {}
        }
      >

          <nav ref={outerNav} className={getStyles().nav} style={minimisedMenu ? { margin: "unset" } : {}}>
            {minimisedMenu &&
              (openMenu ? (
                <BlackCrossMenu
                  onClick={menuIconHandler}
                  className={getStyles()?.responsiveIcon}
                  style={{ height: crossIconHeight, padding: "11px", boxSizing: "unset" }}
                  id={`${props.data?.id}_menuCloseBtn`}
                />
              ) : (
                <Hamburger
                  onClick={menuIconHandler}
                  className={getStyles()?.responsiveIcon}
                  style={{ height: iconHeight, padding: "7px", boxSizing: "unset" }}
                  id={`${props.data?.id}_menuOpenBtn`}
                />
              ))}

            {!minimisedMenu || openMenu ? (
              <ul
                ref={nav}
                className="menu"
                style={
                  minimisedMenu && openMenu
                    ? {
                        flexDirection: "column",
                        position: "absolute",
                        top: 0,
                        zIndex: "2",
                        ...(style?.menuAlignment === "right"
                          ? { right: `calc(${crossIconHeight} + 25px + 2px)`, left: "unset" }
                          : { left: `calc(${crossIconHeight} + 25px + 2px)`, right: "unset" })
                      }
                    : { flexDirection: "row" }
                }
              >
                {navItems?.map((mainMenu, mainMenuCount) => {
                  /* **********************************************************  Main Menu ********************************************************** */
                  return (
                    <>
                      {mainMenu?.elem_type === "MainMenu" && (
                        <li
                          key={mainMenu.id}
                          id={mainMenu.id}
                          elemType={mainMenu.elem_type}
                          onClick={(e) => handleActiveId(e, mainMenu, "submenu")}
                          className={
                            mainMenu.display !== "button" && isActiveElement(mainMenu.id)
                              ? getStyles()?.activeMenu_li
                              : mainMenu.display !== "button"
                              ? getStyles()?.menu_li
                              : null
                          }
                          listitemid={mainMenu.id}
                          style={isMobileMinimisedMenu ? { flexDirection: "column" } : {}}
                        >
                          <span
                            className={
                              mainMenu.display !== "button" && isActiveElement(mainMenu.id)
                                ? getStyles()?.activeMenu_span
                                : mainMenu.display !== "button"
                                ? getStyles()?.menu_span
                                : null
                            }
                            style={{ flexDirection: "column" }}
                          >
                            {mainMenu.display === "button" ? (
                              <CustomButton
                                style={style}
                                label={
                                  <span style={{ display: "flex", alignItems: "center", gap: "5px" }}>
                                    {checkValid(mainMenu.icon.file) && style.showIcons === "true" && (
                                      <img
                                        alt="Img Icon"
                                        src={`${imageBaseURL}${mainMenu?.icon?.file}`}
                                        name={mainMenu.id}
                                        stepIndex={mainMenuCount}
                                        style={{
                                          marginRight: "5px",
                                          height: isActiveElement(mainMenu.id)
                                            ? style.ActiveFontSize
                                            : style.InActiveFontSize
                                        }}
                                      />
                                    )}
                                    {mainMenu?.stepName || mainMenu?.id}
                                    {isChildrenPresent(mainMenu, "submenu") && (
                                      <DownArrow
                                        color={
                                          isActiveElement(mainMenu.id) ? style.ActiveFontColor : style.InActiveFontColor
                                        }
                                        styling={{ marginRight: "0px" }}
                                      />
                                    )}
                                  </span>
                                }
                              />
                            ) : (
                              <span style={{ display: "flex", alignItems: "center", gap: "5px" }}>
                                {checkValid(mainMenu.icon.file) && style.showIcons === "true" && (
                                  <img
                                    alt="Img Icon"
                                    src={`${imageBaseURL}${mainMenu?.icon?.file}`}
                                    name={mainMenu.id}
                                    stepIndex={mainMenuCount}
                                    style={{
                                      marginRight: "5px",
                                      height: isActiveElement(mainMenu.id)
                                        ? style.ActiveFontSize
                                        : style.InActiveFontSize,
                                      width: isActiveElement(mainMenu, "submenu", mainMenu.id)
                                        ? style.ActiveFontSize
                                        : style.InActiveFontSize
                                    }}
                                  />
                                )}
                                {mainMenu?.stepName || mainMenu?.id}

                                {isChildrenPresent(mainMenu, "submenu") && (
                                  <DownArrow
                                    color={
                                      isActiveElement(mainMenu.id) ? style.ActiveFontColor : style.InActiveFontColor
                                    }
                                    styling={{ marginRight: "0px" }}
                                  />
                                )}
                              </span>
                            )}
                            {style.layout === "style1" && mainMenu.display !== "button" && <hr />}
                          </span>

                          <ul
                            className={`submenu ${getStyles()?.submenu}`}
                            style={
                              minimisedMenu
                                ? {
                                    left: "100%",
                                    top: "0",
                                    ...(mobileWidthMatches
                                      ? {
                                          position: "unset",
                                          width: "100%",
                                          animation: "unset",
                                          boxSizing: "border-box",
                                          paddingLeft: "10px"
                                        }
                                      : { position: "absolute" })
                                  }
                                : { left: "0", top: "100%" }
                            }
                          >
                            {mainMenu?.children?.map((submenu, subchildmenuMapIndex) => {
                              /* **********************************************************  Sub Menu ********************************************************** */
                              return (
                                <>
                                  {submenu?.elem_type === "SubMenu" && (
                                    <li
                                      key={submenu.id}
                                      id={submenu.id}
                                      elemType={submenu.elem_type}
                                      onClick={(e) => handleActiveId(e, submenu, "subchildmenu")}
                                      className={
                                        isActiveElement(submenu.id)
                                          ? `${getStyles()?.activeMenu_li}`
                                          : `${getStyles()?.menu_li}`
                                      }
                                      listitemid={submenu.id}
                                      style={
                                        isMobileMinimisedMenu
                                          ? { flexDirection: "column", padding: "unset" }
                                          : { position: "initial" }
                                      }
                                    >
                                      <span
                                        style={isMobileMinimisedMenu ? { padding: "10px 1.5em" } : {}}
                                        className={
                                          isActiveElement(submenu.id)
                                            ? `${getStyles()?.activeSubMenu_span}`
                                            : `${getStyles()?.subMenu_span}`
                                        }
                                      >
                                        <span> {submenu?.substepName || submenu?.id} </span>
                                        {isChildrenPresent(submenu, "subchildmenu") &&
                                          (translationStyles1 ? (
                                            <ChevronLeftIcon fontSize="small" />
                                          ) : (
                                            <ChevronRightIcon fontSize="small" />
                                          ))}
                                      </span>
                                      <ul
                                        className={[
                                          "childMenu",
                                          ...(translationStyles1 ? ["childMenu_ar"] : ["childMenu_en"])
                                        ].join(" ")}
                                        style={
                                          isMobileMinimisedMenu
                                            ? { position: "unset", animation: "unset", boxShadow: "unset" }
                                            : {
                                                position: "absolute",
                                                ...(submenu?.children?.length > subchildmenuMapIndex
                                                  ? {}
                                                  : { top: "unset", bottom: "0" })
                                              }
                                        }
                                      >
                                        {submenu?.children?.map((childmenu) => {
                                          /* **********************************************************  Child Menu ********************************************************** */
                                          return (
                                            <>
                                              {childmenu?.elem_type === "SubChildMenu" && (
                                                <li
                                                  key={childmenu.id}
                                                  id={childmenu.id}
                                                  elemType={childmenu.elem_type}
                                                  onClick={(e) => handleActiveId(e)}
                                                  listitemid={childmenu.id}
                                                  className={
                                                    isActiveElement(childmenu.id)
                                                      ? `${getStyles()?.activeMenu_li}`
                                                      : `${getStyles()?.menu_li}`
                                                  }
                                                >
                                                  <span
                                                    className={
                                                      isActiveElement(childmenu.id)
                                                        ? `${getStyles()?.activeSubMenu_span}`
                                                        : `${getStyles()?.subMenu_span}`
                                                    }
                                                  >
                                                    {childmenu?.subChildMenuName || childmenu?.id}
                                                  </span>
                                                </li>
                                              )}
                                            </>
                                          );
                                        })}
                                      </ul>
                                    </li>
                                  )}
                                </>
                              );
                            })}
                          </ul>
                        </li>
                      )}
                    </>
                  );
                })}
              </ul>
            ) : null}
          </nav>

          {minimisedMenu ? (
            <span
              className={getStyles()?.activeMenu_li}
              style={{
                color: style.ActiveFontColor === "#fff" ? style.InActiveFontColor : style.ActiveFontColor || "#000",
                ...(style?.menuAlignment === "right" ? { marginLeft: "5px" } : { marginRight: "5px" })
              }}
            >
              {activeJson?.stepName || activeJson?.substepName || activeJson.subChildMenuName || activeJson?.id}
            </span>
          ) : null}
      </header>

      <main
        control_id={props.id}
        style={{ minHeight: `calc(98vh - ${menuHeight}px)`, paddingTop: "10px", paddingBottom: "50px" }}
        onDrop={handleDropOnMenu}
        elem_type={activeJson?.elem_type}
        id={activeJson?.id}
        stepIndex={activeJson?.MainMenuIndex}
        substepIndex={activeJson?.SubMenuIndex || activeJson?.SubMenuIndex === 0 ? activeJson?.SubMenuIndex : "-1"}
        subChildMenuIndex={
          activeJson?.subChildMenuIndex || activeJson?.subChildMenuIndex === 0 ? activeJson?.subChildMenuIndex : "-1"
        }
        extraDetails={getExtraDetails()}
        activeJson={JSON.stringify(getActiveJson())}
        className={`${classes.menuMain} menuMain_Id`}
      >
        <ELEMENTS
          containerId={props.containerId}
          previewmode={props.previewmode}
          data={dataArray}
          stepName={activeJson?.id}
          stepIndex={activeJson?.MainMenuIndex}
          substepIndex={activeJson?.SubMenuIndex}
          subChildMenuIndex={activeJson?.subChildMenuIndex}
        />
      </main>

      {props.previewmode !== true && controlSelected?.id === props?.id && (
        <ElemDeleteControl
          containerId={props.containerId}
          id={props.data.id}
          data={props.data}
          deleteControl={deleteControl}
          elemType={props.data.elem_type}
          setDragging={props.setDragging}
          handleReorderStart={props.handleReorderStart}
          handleReorderDrop={props.handleReorderDrop}
        />
      )}

      {props.data?.children?.length > 0 && window?.openQuickTools && open && (
        <QuickTools
          containerId={props.containerId}
          key={props.data.id}
          selectedControl={props.data}
          open={open}
          setOpen={setOpen}
        />
      )}

      {alertData.open === true && <CustomizedAlert alertData={alertData} />}
    </div>
  );

  return props.previewmode ? (props?.data?.style?.["Visible"] === "true" ? MenuJSX : null) : MenuJSX;
}

export default Menu;
